import Table from '../../mo/models';

export default function getVisibleCellOfRow(table: Table, rowIndex: number) {
  const row = table.nodes[rowIndex];
  if (!row) {
    return null;
  }
  for (let i = 0; i < row.nodes.length; i += 1) {
    const cell = row.nodes[i];
    if (cell && !cell.data.hidden) {
      return cell;
    }
  }
  return null;
}
